import React, { useState, useEffect } from 'react';

const ProfileStatusWithHooks = (props) => {
    let [editMode, setEditMode] = useState(false);
    let [status, setStatus] = useState(props.status);

    useEffect(() => {
        setStatus(props.status);
    }, [props.status]);

    const activateEditMode = () => {
        setEditMode(true);
    }

    const deActivateEditMode = () => {
        setEditMode(false);
        props.updateStatus(status);
    }

    const onStatusChange = (e) => {
        setStatus(e.currentTarget.value);
    }

    return (
        <>
            {!editMode &&
                <div className="profile__status">
                    <span onDoubleClick={activateEditMode} className="profile__status"> {status || 'create status'} </span>
                </div>
            }
            {editMode &&
                <div className="profile__status">
                    <input className='status_input' spellcheck="false" onChange={onStatusChange} onBlur={deActivateEditMode} autoFocus={true} value={status}/> 
                </div>
            }
        </>
    );
}

export default ProfileStatusWithHooks;